.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.into-img{
    width: 100%;
    height: 100vh;
    background-size:cover;
    background-position: center;
    object-fit: cover;
}
.sticker{
    position: absolute;
    top: 20%;
    left: 5%;
    width: 150px;
    height: auto;
    z-index: 2;

}
.mask::after{
    content: '';
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.6);
}

.hero{
    height: 100%;
    width: 100%;
}
.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.hero .content h1{
    color: #fff;
    font-size: 3rem;
    font-weight: bold;
    padding: 0rem 0 1.5rem;
}

.hero .content p{
    color: #fff;
    font-size: 1.2rem;
    font-weight:600;
    margin-bottom: 0%;
}

.grid-item { 
    position: relative; 
    border-radius: 0px; 
    overflow: hidden; 
    background: #111; 
    height: 100%; 
    display: flex; 
    cursor: pointer;
    align-items: center; 
    justify-content: center; 
 transform: translateY(0%);
  transition: transform 0.4s ease, box-shadow 0.4s ease;} 
.grid-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transition: transform 0.6s ease;}
.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  padding: 40px 0;
}

.grid-item:hover {
  transform: translateY(-6px);
  box-shadow: 0 20px 40px rgba(0,0,0,0.35);
}


/* Zoom image on hover */
.grid-item:hover img {
  transform: scale(1.08);
}

/* Text overlay */
.card-content {
  position: absolute;
  bottom: 24px;
  left: 24px;
  z-index: 2;
  color: white;
  opacity: 0.9;
  transition: opacity 0.4s ease, transform 0.4s ease;
}

/* Fade + slight rise on hover */
.grid-item:hover .card-content {
  opacity: 1;
  transform: translateY(-4px);
}

/* Typography */
.card-eyebrow {
  font-size: 14px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  margin: 0 0 6px;
  opacity: 0.85;
}

.card-title {
  font-size: 26px;
  font-weight: 600;
  margin: 0 0 4px;
}

.card-subtitle {
  font-size: 16px;
  opacity: 0.85;
  margin: 0;
}

@media screen and (max-width:640px){
    .hero .content h1{
        font-size: 2rem;
    }
    .hero .content p{
        font-size: 1.4rem;
    }
}