.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container{
    max-width: 1140px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
}

.project-card{
    background: rgb(156, 86, 86);
    padding: 1.2rem 1rem;
}
.project-card img{
    width: 100%;
}

.project-title{
    color:white;
    padding: 1rem 0;
}

.pro-details p{
    padding-bottom: 1rem;
    font-size:1.1rem;
    text-align: justify;
}
.pro-buttons .button-40{
    display: flex;
    justify-content: center;
    gap: 1rem 0;
}

@media screen  and (max-width: 740px) {
    .project-container{
        max-width:90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
    
}