.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    
    position: fixed;
    width: 100%;    
    height:90px;
    z-index: 100;
}


.header-bg{
    background: rgba(0,0,0,0.85);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );
    transition: .3s;
}
.nav-menu{
    display: flex;
    list-style: none;

}
.nav-menu li{
    padding: 0 1rem;
    
}

.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
    padding: 0 1rem;
}

.hamburger{
    display: none;
    cursor: pointer;
}
@media screen  and (max-width:1040px) {
    .nav-menu{
        flex-direction:column;
        align-items:center;
        justify-content: center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index:-3;
        transition: .3s;
    }
    .nav-menu.active{
        left:0%;
    }
    .nav-menu li{
        padding: 1rem 0;}

    .nav-menu li a{
        font-size: 2rem;
    }
    .hamburger{
    display: initial;}
    
}

code {
  font-family: 'Montserrat', monospace;
}
