.about{
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about .left{
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px;
}

.h1{
    font-size: 2.5rem;
    margin-bottom: 1rem;
}
.p{
    color: black;
}

.about .left .p{
    margin: 1.2rem 0;
}

.about .right{
    max-width: 700px;
}

.right .img-container{
    display:grid;
    grid-template-columns: repeat(12,1fr);
    position: relative;
    align-items: center;
    text-align: center;
}

.right .img{
    max-width: 90%;
    border: 1px solid #000;
}

.right .top{
    grid-column: 1/ span 8;
    grid-row: 1;
    z-index:6;
    padding-top: 20%;
}

.right .bottom{
    grid-column: 4/-1;
    grid-row: 1;
    z-index:5;
}

.fade-out {
  /* This creates a mask that is solid at the top and transparent at the bottom */
  mask-image: linear-gradient(to bottom, black 0%, transparent 50%);
  -webkit-mask-image: linear-gradient(to bottom, black 0%, transparent 50%);
  height: 100vh; /* Make it fill the first page height */
}



.image-container {
  display: flex;               /* Use flexbox layout */
  justify-content: center;     /* Center horizontally */
  align-items: center;         /* Center vertically (if container has height) */
  gap: 20px;                    /* Space between images */
  height: 100vh;                /* Full viewport height for vertical centering */
      /* Optional background */
}

.image-container img {
  max-width: 30%;             /* Limit image size */
  height: auto;                 /* Keep aspect ratio */
  border-radius: 3px;           /* Optional rounded corners */
}
