.hero-img{
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    position: relative
}

.hero-img::before{
    content:"";
    background:url("https://wallpaperaccess.com/full/1595075.jpg") no-repeat center center/cover;
    background-size:cover;
    background-position: center top;
    height: 100%;
    width: 100%;    
    position: absolute;
    top:-50px;
    left:0;
    z-index: -1;}

.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1{
    font-size: 2,5rem;

}
.hero-img p{
    font-size: 1.2rem;
    margin-top: 1rem;
}

@media screen and (max-width:768px){
    .hero-img h1{
        font-size: 1.8rem;
    }
    .hero-img p{
        font-size: 1rem;
    }
}