.footer{
    width:100%;
    padding: 2rem 0;
    background-color: #000;
}
.footer-container{
    max-width: 1200px;
    margin:auto;
    display: flex;
    text-align: center;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:40px;
}
.left{
    height:100%;
    display: flex;
    flex-direction: column; 
    max-width: 100%;}
.right{
    height:100%;
    display: flex;
    flex-direction: column; 
    max-width: 100%;}
