.carousel {
    margin: 100px auto;
    width: 90%;
    border: 5px solid rgb(19, 153, 186,.2); /* Your debug border */
    overflow: hidden;      /* Hide the scrollbars so it looks like a movie */
    display: flex;
}

.track {
    display: flex;
    width: fit-content;    /* Let the content define the width */
    animation: scroll 10s linear infinite; /* Slower is usually better for logos/cards */
}

.group {
    display: flex;
    gap: 1em;              /* Space between cards */
    padding-right: 1em;    /* Match the gap so the loop is seamless */
}

.card {
    flex: 0 0 150px;       /* Fixed width works best for carousels */
    height: 50vh;
    background-size: cover;
    background-position: center;
    border-radius: 8px;
}

/* The Animation */
@keyframes scroll {
    from { transform: translateX(0); }
    to { transform: translateX(-50%); } /* Move exactly half the total width */
}

